import Tag from '../tag/tag.vue'
let keys = 0;
export default {
	components: {
		Tag
	},
	data() {
		return {
			refreshing: false,
			lists: [],
			imgList: [],
			fetchPageNum: 1,
			img_root: "https://bz.9188u.com/wallpaper_files/"
		}
	},
	onLoad() {
		this.getData();
		uni.getProvider({
			service: 'share',
			success: (e) => {
				let data = [];
				for (let i = 0; i < e.provider.length; i++) {
					switch (e.provider[i]) {
						case 'weixin':
							data.push({
								name: '分享到微信好友',
								id: 'weixin'
							});
							data.push({
								name: '分享到微信朋友圈',
								id: 'weixin',
								type: 'WXSenceTimeline'
							});
							break;
						case 'qq':
							data.push({
								name: '分享到QQ',
								id: 'qq'
							});
							break;
						default:
							break;
					}
				}
				this.providerList = data;
			},
			fail: (e) => {
				console.log('获取登录通道失败', e);
			}
		});
	},
	onPullDownRefresh() {
		console.log('下拉刷新');
		this.refreshing = true;
		this.getData();
	},
	onReachBottom() {
		this.getData();
	},
	methods: {
		change(e) {
			this.current = e.detail.current;
		},
		getData() {
			uni.request({
				url: "https://bz.9188u.com/getImg?size=21&classify=背景图&page=" + (this.refreshing ? 1 : this.fetchPageNum),
				success: (res) => {
					if (res.statusCode !== 200) {
						console.log('请求失败。');
					} else {
						if (this.refreshing && res.data.list[0].id === this.imgList[0].id) {
							uni.showToast({
								title: '已是最新列表',
								icon: 'none',
							})
							this.refreshing = false;
							uni.stopPullDownRefresh();
							return;
						}
						if (this.refreshing) {
							this.refreshing = false;
							uni.stopPullDownRefresh();
							this.imgList = res.data.list;
							this.fetchPageNum = 2;
						} else {
							this.imgList = this.imgList.concat(res.data.list);
							console.log(this.imgList)
							this.fetchPageNum += 1;
						}
					}
					// this.imgList = res.data.list;
				},
				fail: () => {
					uni.showModal({
						content: "请求失败，请重试!",
						showCancel: false
					})
				}
			})
		},
		goDetail(e) {
			// uni.navigateTo({
			// 	url: '../detail/detail?data=' + encodeURIComponent(JSON.stringify(e))
			// })
		},
		share(e) {
			if (this.providerList.length === 0) {
				uni.showModal({
					title: '当前环境无分享渠道!',
					showCancel: false
				})
				return;
			}
			let itemList = this.providerList.map(function(value) {
				return value.name
			})
			uni.showActionSheet({
				itemList: itemList,
				success: (res) => {
					uni.share({
						provider: this.providerList[res.tapIndex].id,
						scene: this.providerList[res.tapIndex].type && this.providerList[res.tapIndex].type === 'WXSenceTimeline' ?
							'WXSenceTimeline' : 'WXSceneSession',
						type: 0,
						title: '',
						summary: e.title,
						imageUrl: e.img_src,
						href: 'https://uniapp.dcloud.io',
						success: (res) => {
							console.log('success:' + JSON.stringify(res));
						},
						fail: (e) => {
							uni.showModal({
								content: e.errMsg,
								showCancel: false
							})
						}
					});
				}
			})
		}
	}
}
